@import url(https://fonts.googleapis.com/css?family=Roboto);
.task-text-field-container {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  padding: 15px;
  margin: 10px 20%;
}

.task-text-field-container input[type="text"] {
  height: 30px;
  background: #ecf0f1;
}

.task-text-field-container textarea {
  background: #ecf0f1;
}

.task-text-field-container .flex-container {
  display: flex;
  justify-content: left;
  align-items: center;
}

.task-text-field-container .flex-container > .flex-item {
  width: 175px;
  display: flex;
  justify-content: left;
}

.task-text-field-container .flex-container > .raw-item {
  width: 100%;
  resize: none;
  padding-left: 5px;
}

.task-text-field-container .flex-container > .raw-item::-webkit-input-placeholder {
  font-size: 13px;
  font-family: monospace;
}

.task-text-field-container .flex-container > .raw-item::-moz-placeholder {
  font-size: 13px;
  font-family: monospace;
}

.task-text-field-container .flex-container > .raw-item::-ms-input-placeholder {
  font-size: 13px;
  font-family: monospace;
}

.task-text-field-container .flex-container > .raw-item::placeholder {
  font-size: 13px;
  font-family: monospace;
}

.task-text-field-container .button-submit-container {
  display: flex;
  justify-content: center;
}

.task-text-field-container .button-submit {
  border: none;
  background-color: rgb(6, 133, 135);
  color: white;
  font-size: 14px;
  margin: 20px;
  padding: 10px 20px;
  cursor: pointer;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  -webkit-transition: background-color 0.5s;
  transition: background-color 0.5s;
}

.task-text-field-container .button-submit:hover {
  background-color: rgb(0, 80, 82);
}

/* accordion styles */
.task-text-field-button-more {
  position: relative;
  float: right;
  bottom: 20px;
  right: 20px;
}

/* input date */
.flex-container input[type="date"] {
  height: 30px;
}

/* date calendar */

/* Removes the clear button from date inputs */
.flex-container input[type="date"]::-webkit-clear-button {
  display: none;
}

/* Removes the spin button */
.flex-container input[type="date"]::-webkit-inner-spin-button {
  display: none;
}

/* Always display the drop down caret */
.flex-container input[type="date"]::-webkit-calendar-picker-indicator {
  color: #2c3e50;
}

/* A few custom styles for date inputs */
.flex-container input[type="date"] {
  -moz-appearance: none;
       appearance: none;
  -webkit-appearance: none;
  color: #95a5a6;
  font-family: "Helvetica", arial, sans-serif;
  font-size: 18px;
  border: 1px solid #ecf0f1;
  background: #ecf0f1;
  padding: 5px;
  display: inline-block !important;
  visibility: visible !important;
}

.flex-container input[type="date"],
focus {
  color: #95a5a6;
  border: 1px solid rgb(169, 169, 169);
  box-shadow: none;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
}

.task-row-container {
  padding: 2px;
  display: grid;
  grid-template-columns: 1fr 50px;
}

.task-row-remove-button {
  color: white;
  background-color: rgb(255, 67, 67);
  border: none;
  padding: 0;
  cursor: pointer;
  -webkit-transition: background-color 0.5s;
  transition: background-color 0.5s;
  font-size: 22px;
  max-height: 47px;
}

.task-row-remove-button:hover {
  background-color: rgb(255, 0, 0);
}

.task-row-container .accordion {
  background-color: #eee;
  color: #444;
  padding: 14px 0;
  width: 100%;
  border: none;
  text-align: left;
  outline: none;
  font-size: 15px;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  max-height: 47px;
}

.task-row-container .active,
.accordion:hover {
  background-color: #ccc;
}

.task-row-container .accordion:after {
  color: #777;
  font-weight: bold;
  float: right;
  margin-left: 5px;
}

.task-row-container .up-down-arrow {
  cursor: pointer;
  padding: 12px 0;
  text-align: center;
  font-size: 22px;
  width: 40px;
}

.panel {
  margin-top: 5px;
  background-color: white;
  max-height: 0;
  overflow: hidden;
  -webkit-transition: max-height 0.2s ease-out;
  transition: max-height 0.2s ease-out;
}

.panelActive {
  width: 100%;
  max-height: null;
}

.panelNotActive {
  width: 100%;
  max-height: 300px;
}

.description {
  height: 100px;
  width: 99%;
  resize: none;
}

.task-row-description-save__button {
  position: relative;
  float: right;
  border: none;
  color: white;
  background-color: #086dd6;
  margin: 5px 10px;
  padding: 5px 10px;
  -webkit-transition: background-color 0.5s;
  transition: background-color 0.5s;
}

.task-row-description-save__button:hover {
  background-color: #064e9b;
}

