.task-row-container-items {
  padding: 0 10px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  height: 19px;
}

.task-row-container-items > input[type="text"] {
  margin: 0 10px;
}

.task-row-item {
  display: grid;
  grid-template-columns: 30px 1fr 40px;
}

/* input styles */
.task-row-checkbox {
  display: flex;
  justify-content: center;
  align-items: center;
  max-height: 47px;
}

.footer-actions-container {
  display: flex;
  justify-content: center;
}

.footer-actions-radio-button-group {
  display: flex;
  align-items: center;
  margin: 0 20px;
}

.footer-actions-radio-button {
  width: 50px;
  margin-right: 10px;
}

.footer-actions-clear-complete__button {
  cursor: pointer;
  color: white;
  height: 30px;
  margin: 5px;
  padding: 0 10px;
  border: none;
  background-color: rgb(255, 67, 67);
  -webkit-transition: background-color 0.5s;
  transition: background-color 0.5s;
}

.footer-actions-clear-complete__button:hover {
  background-color: rgb(255, 0, 0);
}

.footer-actions-done-all__button {
  cursor: pointer;
  color: white;
  height: 30px;
  border: none;
  padding: 0 10px;
  margin: 5px;
  background-color: #086dd6;
  -webkit-transition: background-color 0.5s;
  transition: background-color 0.5s;
}

.footer-actions-done-all__button:hover {
  background-color: #064e9b;
}

.footer-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.footer-divider {
  margin-bottom: 20px;
}

.footer-items-count {
  align-self: center;
}

.task-list-container {
  margin-top: 25px;
}

.app-container {
  font-family: "Roboto", sans-serif;
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.app-content {
  padding: 20px;
  width: 70%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

